.aqi-wrapper {
  max-width: 900px;
  margin: 0 auto;
}

.aqi-wrapper .city-image-wrapper {
  width: 60px;
  height: 60px;
  margin: 0 8px;
}

.aqi-wrapper .city-image-wrapper img {
  width: 60px;
  height: 60px;
  border-radius: 60px;
}

.aqi-wrapper .cell-data {
  display: flex;
  flex-flow: row;
  align-items: center;
  margin: 15px 0;
  padding: 10px;
}

.aqi-wrapper .city-name {
  padding: 0 10px 10px 18%;
  color: #007bff;
}
.aqi-wrapper .city-aqi {
  text-align: right;
}

.aqi-wrapper .city-image-wrapper,
.aqi-wrapper .city-name,
.aqi-wrapper .city-aqi {
  flex-basis: 33%;
}

.aqi-wrapper .good {
  color: olivedrab;
}

.aqi-wrapper .moderate {
  color: gold;
}

.aqi-wrapper .unhealthy-for-sensitive-groups {
  color: chocolate;
}

.aqi-wrapper .unhealthy {
  color: tomato;
}
.aqi-wrapper .very-unhealthy {
  color: purple;
}
.aqi-wrapper .hazardous {
  color: maroon;
}
